/* --- Estilos Globales --- */
body {
    font-family: 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
    background-color: #f8f9fa;
    margin: 0;
    color: #333;
    line-height: 1.6;
}

header {
    background: #2c3e50;
    color: white;
    padding: 30px 20px;
    text-align: center;
    box-shadow: 0 4px 10px rgba(0,0,0,0.2);
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header h1 {
    margin: 0;
    font-size: 2.5rem;
    letter-spacing: 3px;
    text-transform: uppercase;
    flex: 1;
    transition: color 0.5s ease;
}

header button, header a {
    color: #4facfe;
    text-decoration: none;
    font-weight: bold;
    font-size: 0.9rem;
    background: none;
    border: none;
    cursor: pointer;
    transition: color 0.3s;
}

header button:hover, header a:hover {
    color: #00f2fe;
}

/* --- Secciones de Contenido --- */
h2 {
    color: #2c3e50;
    border-bottom: 3px solid #287fef;
    display: inline-block;
    padding-bottom: 5px;
    margin-bottom: 30px;
}

section {
    padding: 40px 20px;
    text-align: center;
}

/* --- Cuadrícula de Proyectos --- */
.proyectos-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
}

.proyecto-item {
    background: white;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0,0,0,0.08);
    transition: all 0.3s ease;
    text-align: left;
    display: flex;
    flex-direction: column;
}

.proyecto-item:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 30px rgba(0,0,0,0.15);
}

.proyecto-item img {
    width: 100%;
    height: 230px;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 15px;
}

.proyecto-item h3 {
    margin: 10px 0;
    color: #2c3e50;
}

.proyecto-item p {
    flex-grow: 1;
    margin-bottom: 15px;
}

.btn-container {
    display: flex;
    gap: 8px;
    margin-top: 10px;
}

.btn-container .btn-main,
.btn-container .btn-delete {
    flex: 1;
    padding: 10px 12px;
    font-size: 0.95rem;
    text-align: center;
}

/* --- Buzón de Consultas (Admin) --- */
#seccionMensajesAdmin {
    max-width: 900px;
    margin: 40px auto;
    background: #fff8ef;
    border: 2px solid #ff9800;
    border-radius: 15px;
    padding: 30px;
    text-align: left;
}

.mensaje-card {
    background: white;
    padding: 20px;
    margin-bottom: 15px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-left: 8px solid #ff9800;
    box-shadow: 0 4px 6px rgba(0,0,0,0.05);
    word-wrap: break-word;
    overflow-wrap: break-word;
    word-break: break-word;
}

.mensaje-card .mensaje-texto {
    flex: 1;
    margin-right: 20px;
    min-width: 0;
}

/* --- Formulario de Contacto --- */
.form-container {
    max-width: 600px;
    margin: 60px auto;
    background: white;
    padding: 40px;
    border-radius: 20px;
    box-shadow: 0 10px 40px rgba(0,0,0,0.1);
}

.form-container h2 {
    text-align: center;
}

label {
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
    text-align: left;
    color: #555;
}

input, textarea {
    width: 100%;
    padding: 14px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-sizing: border-box;
    font-size: 1rem;
    background-color: #fcfcfc;
}

input:focus, textarea:focus {
    outline: none;
    border-color: #287fef;
    background-color: white;
    box-shadow: 0 0 8px rgba(40, 127, 239, 0.2);
}

/* --- Botones --- */
.btn-main {
    background: linear-gradient(135deg, #287fef, #1a5bb8);
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.btn-main:hover {
    background: linear-gradient(135deg, #1a5bb8, #13458c);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(40, 127, 239, 0.3);
}

.btn-main[type="submit"] {
    width: 100%;
    padding: 14px;
    font-size: 1.1rem;
}

.btn-edit {
    background: #f1c40f;
    color: #222;
    border: none;
    padding: 10px 18px;
    border-radius: 6px;
    cursor: pointer;
    font-weight: bold;
    font-size: 0.95rem;
    transition: all 0.3s ease;
}

.btn-edit:hover {
    background: #e6b800;
    transform: translateY(-2px);
}

.btn-delete {
    background: #e74c3c;
    color: white;
    border: none;
    padding: 10px 18px;
    border-radius: 6px;
    cursor: pointer;
    font-weight: bold;
    font-size: 0.95rem;
    transition: all 0.3s ease;
}

.btn-delete:hover {
    background: #c0392b;
    transform: translateY(-2px);
}

/* --- Footer --- */
footer {
    background: #1a1a1a;
    color: #ddd;
    padding: 50px 20px;
    margin-top: 40px;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
}

.insta-link {
    color: #E1306C;
    font-size: 3rem;
    text-decoration: none;
    display: inline-block;
    transition: transform 0.3s;
}

.insta-link:hover {
    transform: scale(1.2);
}

/* Sección de clima (minimalista) */
#climaBox {
    max-width: 360px;
    margin: 20px auto;
    background: transparent;
    color: #333;
    padding: 10px 15px;
    border-radius: 6px;
    text-align: center;
    border: 1px solid #ccc;
}

#climaBox h2 {
    font-size: 1.2rem;
    margin-bottom: 10px;
}

#climaBox p {
    margin: 4px 0;
    font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 600px) {
    header {
        flex-direction: column;
    }
    
    header h1 { 
        font-size: 1.8rem; 
    }
    
    header button, header a { 
        display: block; 
        margin-top: 10px; 
    }
    
    .mensaje-card { 
        flex-direction: column; 
        align-items: flex-start; 
    }
    
    .mensaje-card .btn-delete { 
        margin-top: 15px; 
        width: 100%; 
    }
}
